import React, { useState } from "react";
import styles from "../styles/CreateProfile.module.css";
import Input from "../components/Input";
import Button from "../components/PrimaryButton";
const CreateProfile = (props) => {
  const [paymentId, setPaymentId] = useState();
  const paymentHandler = () => {
    const url = `https://api.razorpay.com/v1/payments/${paymentId}`;
    const headers = new Headers();
    headers.append(
      "Authorization",
      "Basic" + btoa(`rzp_test_qaf1ptPXgcD7VN:XWDfFQnA4tLwX42oFU2vL7uS`)
    );
    console.log("inside");
    fetch(url, { headers: headers }).then((response) => {
      console.log(response);
    });
  };
  return (
    <div className={styles.wrapper}>
      <div className={styles.loginBox}>
        <h1>Create Profile</h1>
        <Input
          title="payment Id"
          onChangeText={(event) => setPaymentId(event.target.value)}
        />
        <Button title="Next" onClick={paymentHandler} />
      </div>
    </div>
  );
};

export default CreateProfile;
